import React, { Component } from 'react';
import './Contact.css';

class Contact extends Component {
	render() {
		return (
			<div className="contact" id="contact">
				<div className="contact_heading">
					<h1 className="contact_heading_title">
						Get in touch<span className="spn">.</span>
					</h1>
				</div>
				<form className="input_holder">
					<div className="input_row">
						<div className="input_row_col">
							<div className="input_text">
								<input type="text" id="name" required />
								<label htmlFor="name">*Your Name</label>
							</div>
						</div>
						<div className="input_row_col">
							<div className="input_text">
								<input type="email" id="email" required />
								<label htmlFor="email">*Your email</label>
							</div>
						</div>
					</div>
					<div className="input_text massage_input">
						<input type="textfield" id="msg" required />
						<label htmlFor="msg">*Your Massage</label>
					</div>
					<button type="submit">submit</button>
				</form>
			</div>
		);
	}
}

export default Contact;
