import React, { useEffect } from 'react';
import './Portfolio.css';
import photographi from './Portfolios/ptotographi.jpg';
import natour from './Portfolios/natour.jpg';
import nexter from './Portfolios/nexter.jpg';
import trillo from './Portfolios/trillo.jpg';
import smartbrain from './Portfolios/smartbrain.jpg';
import pokedex from './Portfolios/pokedex.jpg';
import gsap from 'gsap';

const Portfolio = () => {
	const porttl = gsap.timeline();
	useEffect(() => {
		porttl
			.to('.loading_holder', 4, { opacity: 0 })
			.to('.loading_holder', 0.1, { width: 0 })
			.from('.portfolio', 1, { opacity: 0 });
	});

	return (
		<div className="portfolio_holder">
			<div className="loading_holder">
				<div className="lds-ripple">
					<div />
					<div />
				</div>
			</div>
			<div className="portfolio">
				<div className="portfolio_heading">
					<h1 className="portfolio_heading_title">Portfolio</h1>
					<p className="portfolio_heading_text">
						Each and every single project I made are from scrach.I tried by best to keep them trendy and
						also use the latest technology.Here are few of my project with I have created from scrach.
					</p>
				</div>

				<div className="portfolio_showcase">
					<div className="portfolio_showcase_item" data-aos="zoom-in" data-aos-duration="1000">
						<img src={trillo} alt="portfolio" className="portfolio_showcase_item_img" />
						<h4>A hotel booking website.</h4>
						<small className="portfolio_showcase_item_detail">
							Trillo is a metasearch hotel search site that quickly sifts through the metadata of hotel
							sites and find best hotels.This website completely build on advance css grid.
						</small>
					</div>

					<div className="portfolio_showcase_item" data-aos="zoom-in" data-aos-duration="1000">
						<img src={natour} alt="portfolio" className="portfolio_showcase_item_img" />
						<h4>Landing page for Tour Organigers.</h4>
						<small className="portfolio_showcase_item_detail">
							A beautiful website for tourist.Which will attract visitor by its beautiful look and feel.A
							simple yet powerful website with complex web animation.
						</small>
					</div>

					<div className="portfolio_showcase_item" data-aos="zoom-in" data-aos-duration="1000">
						<img src={nexter} alt="portfolio" className="portfolio_showcase_item_img" />
						<h4>A realstate agent website.</h4>
						<small className="portfolio_showcase_item_detail">
							A beautiful website for buy,sell or rent realstates.Build on top of advance css flexbox
							which is very responsive and gives unique loook and feel
						</small>
					</div>

					<div className="portfolio_showcase_item" data-aos="zoom-in" data-aos-duration="1000">
						<img src={photographi} alt="portfolio" className="portfolio_showcase_item_img" />
						<h4>Photography Landing Page</h4>
						<small className="portfolio_showcase_item_detail">
							A beautiful single page photography landing page for small besiness.
						</small>
					</div>

					<div className="portfolio_showcase_item" data-aos="zoom-in" data-aos-duration="1000">
						<img src={smartbrain} alt="portfolio" className="portfolio_showcase_item_img" />
						<h4>A React webapp.</h4>
						<small className="portfolio_showcase_item_detail">
							A webapp which may looks simple but it has very complex mechanism.Build on top of
							react.Using the rest api Machine learning technology it detects faces inside the photo.{' '}
						</small>
					</div>

					<div className="portfolio_showcase_item" data-aos="zoom-in" data-aos-duration="1000">
						<img src={pokedex} alt="portfolio" className="portfolio_showcase_item_img" />
						<h4>A portfolio website.</h4>
						<small className="portfolio_showcase_item_detail">
							A beutifull Portfolio website build included many packages and react singlepage router.
						</small>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Portfolio;
