import React, { useEffect } from 'react';
import gsap from 'gsap';
import './Resume.css';
import resumePhoto from './resume-photo.jpg';

const Resume = () => {
	const resumetl = gsap.timeline();
	useEffect(() => {
		resumetl
			.to('.loading_holder', 3, { opacity: 0 })
			.to('.loading_holder', 0.1, { width: 0 })
			.from('.resume_title p', 0.4, { y: -50, opacity: 0 })
			.from('.resume_header', 0.4, { x: 50, opacity: 0 })
			.from('.resume_header-details', 0.4, { x: -100, opacity: 0 })
			.from('.details_para', 0.4, { x: -100, opacity: 0, stagger: 0.3 })
			.from('.mdi', 0.5, { x: 100, opacity: 0, stagger: 0.1 })
			.from('.btn', 0.4, { opacity: 0 })
			.from('.img_holder', 1, { x: 100, opacity: 0, delay: 0.2 })
			.from('.skills_title', 0.5, { opacity: 0, x: -50 })
			.from('.skills_sml', 0.5, { opacity: 0, x: -50 })
			.from('.ui', 0.5, { opacity: 0, x: 50, stagger: 0.2 });
	});

	return (
		<div className="resume" id="resume">
			<div className="loading_holder">
				<div className="lds-ripple">
					<div />
					<div />
				</div>
			</div>
			<div className="resume_title">
				<p>
					Rahat<span>.</span>
				</p>
			</div>

			<div className="resume_row">
				<div className="col-1-of-2 resume_row_info">
					<h1 className="resume_header">
						I'm Mohammad Rahat<span>.</span>
					</h1>
					<p className="resume_header-details">
						I am a Web Developer located in Chittagong, Bangladesh. I currently work as a full time
						freelancer. I am looking to take on more work and to increase my skills as a Web Developer.
					</p>
					<div className="resume_header_info">
						<p className="details_para">
							<span>Phone</span>
							<small>+88017110-79526</small>
						</p>
						<p className="details_para">
							<span>Email</span>
							<small>errahatinfinity@gmail.com</small>
						</p>
						<p className="details_para">
							<span>Address</span>
							<small>Sagorica,Pahartali,Chittagong</small>
						</p>
						<p className="details_para">
							<span>Social</span>
							<small className="icons">
								<i className="mdi mdi-facebook" />
								<i className="mdi mdi-whatsapp" />
								<i className="mdi mdi-instagram" />
								<i className="mdi mdi-linkedin" />
								<i className="mdi mdi-github" />
							</small>
						</p>
					</div>
					<div className="btn_holder">
						<button className="btn">Contact Me</button>
						<button className="btn">Download CV</button>
					</div>
				</div>
				<div className="col-1-of-2 resume_row_image_holder">
					<div className="img_holder">
						<img src={resumePhoto} alt="resume" className="resume_photo" />
					</div>
				</div>
			</div>

			<div className="resume_skills">
				<div className="resume_skills_row">
					<div className="col-1-of-3">
						<h1 className="skills_title">
							Skills<span>.</span>
						</h1>
						<small className="skills_sml">
							Whatever tools need to build an awesome webapp or website for modern age I know most of
							them. The website I'll made will me fully responsive on every devices and pixel sharp
							parfect.
						</small>
					</div>

					<div className="col-1-of-3">
						<div className="skill ui">
							<h1 className="skill_heading">Web Development</h1>
							<small className="skill_text">Javascript, React, NodeJS, Express, Postgress, MangoDB</small>
						</div>
					</div>

					<div className="col-1-of-3">
						<div className="skill ui">
							<h1 className="skill_heading">UI/UX Design</h1>
							<small className="skill_text">
								Photoshop, Illustrator, Sketch, AdobeXD, XMind, Prototyping, Wireframing, User Research,
								Usability Testing.
							</small>
						</div>
					</div>
				</div>

				<div className="resume_skills_row">
					<div className="col-1-of-3 divider">&nbsp;</div>

					<div className=" skillmar col-1-of-3">
						<div className="skill" data-aos="fade-left" data-aos-duration="1000">
							<h1 className="skill_heading ">FrontEnd Development</h1>
							<small className="skill_text">HTML, CSS, LESS, SASS, Bootstrap, Wordpress.</small>
						</div>
					</div>

					<div className="skillmar col-1-of-3">
						<div className="skill" data-aos="fade-left" data-aos-duration="1000">
							<h1 className="skill_heading">Tools</h1>
							<small className="skill_text">Npm, Yarn, Github, VS Code, Terminal, JQuery, Heroku.</small>
						</div>
					</div>
				</div>
			</div>

			<div className="resume_experience">
				<div className="resume_experience_row">
					<div className="col-1-of-3" data-aos="fade-right" data-aos-duration="1000">
						<h1 className="resume_experience_heading">
							Experience<span>.</span>
						</h1>
						<p className="resume_experience_text">
							I partner with startups, organizations and Fortune 500 companies to build digital products
							that help clients overcome challenges and create lasting connections with millions of people
							every day.
						</p>
					</div>

					<div className="col-2-of-3">
						<div className="exp" data-aos="fade-left" data-aos-duration="1000">
							<h1 className="exp_head">
								Web Developer <span>Jaanani Telecom.</span>
							</h1>
							<small>May,2016-present</small>
							<p className="exp_text">
								<i>
									Designed and developed user-friendly website, including optimized check-out page
									that increased user clicks, and subsequently customer purchases by 20%.
								</i>
							</p>
						</div>
						<div className="exp" data-aos="fade-left" data-aos-duration="1000">
							<h1 className="exp_head">
								Middle frontend Developer <span>BD iconics.</span>
							</h1>
							<small>May,2016-jan,2017</small>
							<p className="exp_text">
								<i>
									Leading a small team on a variety of development projects and delivering solutions
									to meet and exceed clients’ briefs.
								</i>
							</p>
						</div>
						<div className="exp" data-aos="fade-left" data-aos-duration="1000">
							<h1 className="exp_head">
								IT Specialist <span>Temtin Co.</span>
							</h1>
							<small>Feb,2017-Nov,1018</small>
							<p className="exp_text">
								<i>
									Revamped web application security applications, minimizing hacker attacks from 2.3%
									to 0.02%.
								</i>
							</p>
						</div>
					</div>
				</div>
			</div>

			<div className="resume_education">
				<div className="resume_experience_row">
					<div className="col-1-of-3" data-aos="fade-right" data-aos-duration="1000">
						<h1 className="resume_experience_heading">
							Education<span>.</span>
						</h1>
						<p className="resume_experience_text">
							All my life I have been driven by my strong belief that education is important. I try to
							learn something new every single day.
						</p>
					</div>

					<div className="col-2-of-3">
						<div className="exp" data-aos="fade-left" data-aos-duration="1000">
							<h1 className="exp_head">
								Bachelor of Computer Science <span>B.Sc</span>
							</h1>
							<small>2016</small>
							<p className="exp_text">
								<i>Completed by B.Sc from the Primier University,Chittagong on 2016.</i>
							</p>
						</div>
						<div className="exp" data-aos="fade-left" data-aos-duration="1000">
							<h1 className="exp_head">
								Master of Computer Science <span>M.Sc</span>
							</h1>
							<small>2017 - present</small>
							<p className="exp_text">
								<i>Currently doing my masters on United International University.</i>
							</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Resume;
