import React, { useEffect } from 'react';
import './Home.css';
import gsap from 'gsap';

const Home = () => {
	const tl = gsap.timeline();

	useEffect(() => {
		tl
			.to('.loading_holder', 4, { opacity: 0 })
			.to('.loading_holder', 0.1, { width: 0 })
			.to('.Home_background', 2, { yPercent: 80, opacity: 1, ease: 'bounce.out' })
			.from('.name1', 1, { x: -100, opacity: 0 })
			.to('.name1spn', 1, { y: -23, opacity: 0 })
			.to('.name2spn', 1, { opacity: 1, y: -33 })
			.to('.name', 1.5, { opacity: 0, y: 34 })
			.to('.namer', 1.5, { opacity: 1, y: 34 })
			.to('.namer', 1.5, { opacity: 0 })
			.to('.Home_background', 2, { yPercent: 0, opacity: 1 })
			.from('.des_spn1,.des_spn2', 1.5, { opacity: 0, x: -100, stagger: 0.4 })
			.from('.des_spn3', 1.5, { opacity: 0 })
			.to('.home_bg_ani', 1, { opacity: 1 });
	});
	return (
		<div className="Home">
			<div className="loading_holder">
				<div className="lds-ripple">
					<div />
					<div />
				</div>
			</div>
			<div className="home_bg_ani">
				<div className="night">
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
					<div className="shooting_star" />
				</div>
			</div>
			<div className="Home_background" />
			<div className="name_holder">
				<h1 className="namer">Mohammad Rahat</h1>
				<h1 className="name">
					<span className="name1">
						Web <span className="name1spn">Developer</span>
						<span className="name2spn">Designer</span>
					</span>
				</h1>
			</div>
			<h1 className="des">
				<span className="des_spn1">Get Your Thing's Done.</span>
				<span className="des_spn1">By Most Skilled Developer.</span>
				<span className="des_spn3">Mohammad Rahat.</span>
			</h1>
		</div>
	);
};

export default Home;
