import React, { useEffect } from 'react';
import gsap from 'gsap';
import './Services.css';

const Services = () => {
	const servtl = gsap.timeline();
	useEffect(() => {
		servtl
			.to('.loading_holder', 4, { opacity: 0 })
			.to('.loading_holder', 0.1, { width: 0 })
			.from('.services', 1, { opacity: 0 });
	});

	return (
		<div className="services_holder">
			<div className="loading_holder">
				<div className="lds-ripple">
					<div />
					<div />
				</div>
			</div>
			<div className="services" id="services">
				<div className="services_header">
					<h1 className="services_header_title">
						Our Services <span className="spn">.</span>
					</h1>
					<p className="services_header_text">
						Our web experiences are high-performing, feature-packed and digitally transformative, designed
						to be user-friendly, fully functional, very secure and able to scale as your enterprise grows.
					</p>
				</div>

				<div className="services_container">
					<div className="service_item" data-aos="fade-right" data-aos-duration="1000">
						<i className="service_item_icon fal fa-browser">
							<hr />
						</i>
						<h3 className="service_item_title">Web Design.</h3>
						<p className="service_item_details">Beautiful and responsive front end web design.</p>
					</div>

					<div className="service_item" data-aos="zoom-in" data-aos-duration="1000">
						<i className="service_item_icon fal fa-code">
							<hr />
						</i>
						<h3 className="service_item_title">Web Development</h3>
						<p className="service_item_details">Backend web development for complex website.</p>
					</div>

					<div className="service_item" data-aos="fade-left" data-aos-duration="1000">
						<i className="service_item_icon fal fa-pencil-ruler">
							<hr />
						</i>
						<h3 className="service_item_title">Graphics Design</h3>
						<p className="service_item_details">Unique and beautiful design and vector illustration.</p>
					</div>

					<div className="service_item" data-aos="fade-right" data-aos-duration="1000">
						<i className="service_item_icon fal fa-file-edit">
							<hr />
						</i>
						<h3 className="service_item_title">Easy to customize.</h3>
						<p className="service_item_details">
							Clean and easy to understand source code.Admin panel to customize overall website.
						</p>
					</div>

					<div className="service_item" data-aos="zoom-in" data-aos-duration="1000">
						{' '}
						<i className="service_item_icon fal fa-cogs">
							<hr />
						</i>
						<h3 className="service_item_title">Fix websites</h3>
						<p className="service_item_details">
							Incompleted website or debug pre-build webpage services are here.
						</p>
					</div>

					<div className="service_item" data-aos="fade-left" data-aos-duration="1000">
						<i className="service_item_icon fal fa-pencil-paintbrush">
							<hr />
						</i>
						<h3 className="service_item_title">Psd to Website.</h3>
						<p className="service_item_details">
							Convert any kind of graphical template into a functional webpage.
						</p>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Services;
