import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Nav.css';

class Nav extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isChecked : false
		};
	}

	toggleCheck = () => {
		this.setState((prevState) => {
			return { isChecked: !prevState.isChecked };
		});
	};

	clickHandler = () => {
		this.setState((prevState) => {
			return { isChecked: false };
		});
	};

	render() {
		return (
			<div className="navigation">
				<input
					type="checkbox"
					checked={this.state.isChecked}
					onChange={this.toggleCheck}
					className="navigation_checkbox"
					id="navi-toggle"
				/>
				<label htmlFor="navi-toggle" className="navigation_button">
					<span className="navigation_button_icon">&nbsp;</span>
				</label>
				<div className="navigation_background">&nbsp;</div>

				<nav className="navigation_nav">
					<ul className="navigation_nav_list">
						<li className="navigation_nav_list_item">
							<Link to="/ralfolio" onClick={this.clickHandler} className="navigation_nav_list_link">
								Home
							</Link>
						</li>

						<li className="navigation_nav_list_item">
							<Link to="/resume" onClick={this.clickHandler} className="navigation_nav_list_link">
								Resume
							</Link>
						</li>

						<li className="navigation_nav_list_item">
							<Link to="/services" onClick={this.clickHandler} className="navigation_nav_list_link">
								Services
							</Link>
						</li>

						<li className="navigation_nav_list_item">
							<Link to="/portfolio" onClick={this.clickHandler} className="navigation_nav_list_link">
								Portfolio
							</Link>
						</li>

						<li className="navigation_nav_list_item">
							<Link to="/contact" onClick={this.clickHandler} className="navigation_nav_list_link">
								Contact
							</Link>
						</li>
					</ul>
				</nav>
			</div>
		);
	}
}

export default Nav;
